import React, { useRef, useState, useEffect } from 'react';
import './App.css';
import { useRive, useStateMachineInput } from "@rive-app/react-canvas";
import womensDayRive from './womens-day.riv';

function App() {
  const canvasRef = useRef(null);
  const { rive, RiveComponent } = useRive({
    src: womensDayRive,
    autoplay: true,
    stateMachines: "State Machine",
  });

  const maleInput = useStateMachineInput(rive, "State Machine", "Male");
  const femaleInput = useStateMachineInput(rive, "State Machine", "Female");

  const [maleValue, setMaleValue] = useState(66);
  const [femaleValue, setFemaleValue] = useState(34);
  const [headlineText, setHeadlineText] = useState("Woman at german universities with habilitation");

  useEffect(() => {
    if (maleInput && femaleInput) {
      maleInput.value = maleValue;
      femaleInput.value = femaleValue;
    }

    if (rive) {
      rive.setTextRunValue("malePercentage", `${maleValue}`);
      rive.setTextRunValue("femalePercentage", `${femaleValue}`);
      rive.setTextRunValue("womensDayHeadline", headlineText);
    }
  }, [rive, maleValue, femaleValue, headlineText]);

  const handleValueChange = (gender, event) => {
    const value = Math.min(Math.max(0, event.target.value), 100);
    if (gender === 'male') {
      setMaleValue(value);
      setFemaleValue(100 - value);
    } else if (gender === 'female') {
      setFemaleValue(value);
      setMaleValue(100 - value);
    }
  };

  const incrementValue = (gender) => {
    handleValueChange(gender, { target: { value: (gender === 'male' ? maleValue : femaleValue) + 1 } });
  };

  const decrementValue = (gender) => {
    handleValueChange(gender, { target: { value: (gender === 'male' ? maleValue : femaleValue) - 1 } });
  };

  const exportCanvasAsPNG = () => {
    const canvas = canvasRef.current.querySelector('canvas');
    if (!canvas) {
      console.error('Canvas-Element wurde nicht gefunden.');
      return;
    }
    const imageURL = canvas.toDataURL('image/png');
    const link = document.createElement('a');
    link.href = imageURL;
    link.download = 'rive-animation.png';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  return (
    <div className="App">
      <div className="App-canvas-container" ref={canvasRef}>
        <RiveComponent style={{ width: '100%', height: '100%' }} />
      </div>
      <div className="App-inputs-container">
        <h1>Equal?</h1>
        <p>create your equality chart</p>
        <div className="input-group">
          <label>Male in % </label>
          <button onClick={() => incrementValue('male')}>+</button>
          <input 
            type="number" 
            value={maleValue} 
            onChange={(e) => handleValueChange('male', e)} 
            min="0" max="100"
          />
          <button onClick={() => decrementValue('male')}>-</button>
        </div>
        <div className="input-group">
          <label>Female in % </label>
          <button onClick={() => incrementValue('female')}>+</button>
          <input 
            type="number" 
            value={femaleValue} 
            onChange={(e) => handleValueChange('female', e)} 
            min="0" max="100"
          />
          <button onClick={() => decrementValue('female')}>-</button>
        </div>
        <div className="input-group">
          <label>Write Headline </label>
          <input
            type="text"
            value={headlineText}
            onChange={(e) => setHeadlineText(e.target.value)}
            placeholder="Update Headline"
          />
        </div>
        <button className="input-group" onClick={exportCanvasAsPNG}>Download</button>
        <h3>Sources</h3>
        <a href="https://eige.europa.eu/gender-equality-index/2023/DE">Eige Gender Equality Index EU</a>
      </div>
    </div>
  );
}

export default App;
